@media screen and (min-width: 1284px) {
	.sidebar-control {
		display: none !important;
	}
}

.inventory-page-header {
	display: flex;
	flex-flow: row wrap;
	justify-content: space-between;
	margin-bottom: 0.5rem;
	padding-left: 1.5rem;
}

.inventory-page-header h2 {
	color: var(--theme-color);
	font-weight: bolder;
	text-align: center;
	display: inline-block;
	margin-bottom: 5px;
	margin-right: 4.25rem;
}

.inventory-page-subheader-1 {
	display: flex;
	flex-flow: row wrap;
	align-items: center;
}

.inventory-page-subheader-2 {
	display: flex;
	flex-flow: row nowrap;
	justify-content: flex-end;
	align-items: center;
	flex-grow: 1;
}

.inventory-page-body {
	display: flex;
	justify-content: flex-start;
	flex-flow: row nowrap;
	overflow: hidden;
}

.category-sidebar {
	min-width: 200px;
	box-shadow: 2px 0 3px 0 rgba(0, 0, 0, 0.25);
	background-color: #FFFFFF;
	border: 1px solid #DDD;
	margin-right: 2.5rem;
	padding-bottom: 23.25vh;
	position: relative;
	transition: all 0.5s ease;
}

.category-sidebar.hidden {
	margin-left: -200px;
	box-shadow: none;
}

.category-sidebar.hidden li.active {
	transform: none;
}

.category-sidebar.hidden .arrow-right {
	display: none;
}

.category-list-wrapper {
	max-height: 75%;
	transition: all 0.5s ease-in;
}

.category-list-wrapper.collapsed {
	max-height: 0;
	overflow: hidden;
    transition: all 0.5s ease-out;
}

.category-sidebar ul {
	width: 100%;
    display: flex;
    flex-direction: column;
   
	line-height: 0;
	list-style: none;
	margin-bottom: 0;
    padding-inline-start: 0px;
}

.category-sidebar h3 {
	display: flex;
	flex-flow: row nowrap;
	justify-content: center;
	align-items: center;
	font-size: 22px;
	font-weight: bold;
	margin-top: 0.5rem;
	margin-bottom: 0.5rem;
}

.category-sidebar h3 img {
	width: 20px;
	margin-right: 8px;
	transition: all 0.2s ease-in;
}

.category-sidebar h3.collapsed img {
	transform: rotate(-90deg);
}

.category-sidebar ul li {
    width: 100%;
    margin-top: 1px;
	cursor: pointer;
	padding: 20px 1rem;
	transition: all .3s ease-out;
	position: relative;
}

.category-sidebar ul li:hover,
.category-sidebar ul li.active {
	transform: scale(1.05);
	border-radius: 0.1cm;
}

.category-sidebar ul li.active .arrow-right {
	height: 45px;
	width: 45px;
	position: absolute;
	right: -16px;
	bottom: -3px;
	transform: rotate(45deg);
}

.sidebar-control {
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	width: 50px;
	height: 50px;
	bottom: 0.5em;
	left: 0.5em;
	background-color: var(--theme-color);
	border-radius: 50%;
	opacity: 0.75;
	cursor: pointer;
	z-index: 9999;
}

.sidebar-control.hidden {
	transform: rotate(180deg);
}

.sidebar-control img {
	width: 25px;
	height: 25px;
}

.inventory-list {
	display: flex;
	flex-flow: row wrap;
	justify-content: space-around;
	align-content: flex-start;
}

.inventory-list .card {
	display: inline-flex;
	padding: 1rem;
	width: 200px;
	height: 250px;
	margin-right: 2rem;
	margin-bottom: 2rem;
	cursor: pointer;
	position: relative;
	box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, 0.25);
}

.card .cardImage {
	width: 100%;
	height: 75%;
	display: flex;
	align-items: center;
	overflow: hidden;
	margin-bottom: 3px;
}

.card .cardImage img {
	width: 100%;
	border-radius: 7%;
}

.card .card-text {
	margin: 0;
	width: 100%;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
}

.card .card-text.light {
	font-size: 12px;
	font-weight: normal;
}

.card .cardQty {
	display: inline-block;
	width: auto;
	position: absolute;
	bottom: 0.35rem;
	right: 0.75rem;
}

.qty {
	color: var(--theme-color);
}

.warning {
	color: red;
	font-size: smaller;
}

#categoryLeft {
	width: 50%;
}
#categoryRight {
	width: 50%;
}

.modalCategoryName {
	padding: 0.3em;
}

.alittletotheleft {
	padding: 0.3em;
}

.categorySubTitle {
	padding-top: 0.5em;
	padding-left: 2.3em;
}

.history-item {
	border-left: 4px solid;
	border-top-left-radius: 2px;
	border-bottom-left-radius: 2px;
	background-color: #EFEFEF;
	margin-bottom: 10px;
}

.history-list {
	max-height: 200px;
	overflow-y: scroll;
	/* display: flex; */
}

.history-list ul {
	width: 90%;
	list-style: none;
	display: inline-block;
}
.history-list p {
	display: inline-block;
}